<template><section><h1>Dialog 对话框</h1>
<h2>基本用法</h2>
<div class="demo-block">
<vi-button type="text" @click="dialogPage.dialogVisible1 = true">点击打开 Dialog1</vi-button>
<vi-dialog title="提示" :visible.sync="dialogPage.dialogVisible1" width="30%" :before-close="beforeHandleClose1">
  <span>dialog1 这是一段信息</span>
  <span slot="footer" class="dialog-footer">
    <vi-button @click="dialogPage.dialogVisible1 = false">取 消</vi-button>
    <vi-button type="primary" @click="dialogPage.dialogVisible1 = false">确 定</vi-button>
  </span>
</vi-dialog>
</div>
<blockquote>
<p>注意：如果 visible 属性绑定的变量位于 Vuex 的 store 内，那么 .sync 不会正常工作。此时需要去除 .sync 修饰符，同时监听 Dialog 的 open 和 close 事件，在事件回调中执行 Vuex 中对应的 mutation 更新 visible 属性绑定的变量的值。</p>
</blockquote>
<demo-block>
                        <div slot="desc"><html><head></head><body><div class="demo-block">
<vi-button type="text" @click="dialogPage.dialogVisible2 = true">点击打开 Dialog</vi-button>
<vi-dialog title="提示" :visible.sync="dialogPage.dialogVisible2" width="30%" :before-close="beforeHandleClose1">
  <span>这是一段信息</span>
  <span slot="footer" class="dialog-footer">
    <vi-button @click="dialogPage.dialogVisible2 = false">取 消</vi-button>
    <vi-button type="primary" @click="dialogPage.dialogVisible2 = false">确 定</vi-button>
  </span>
</vi-dialog>
</div>

</body></html></div>
