<span class="hljs-tag">&lt;<span class="hljs-name">vi-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"dialogPage.dialogVisible2 = true"</span>&gt;</span>点击打开 Dialog<span class="hljs-tag">&lt;/<span class="hljs-name">vi-button</span>&gt;</span>
