  <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"footer"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"dialog-footer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vi-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"dialogPage.dialogVisible2 = false"</span>&gt;</span>取 消<span class="hljs-tag">&lt;/<span class="hljs-name">vi-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vi-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"dialogPage.dialogVisible2 = false"</span>&gt;</span>确 定<span class="hljs-tag">&lt;/<span class="hljs-name">vi-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">vi-dialog</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">dialogPage</span>: {
        <span class="hljs-attr">dialogVisible1</span>: <span class="hljs-literal">false</span>
      }
    }
  },
  <span class="hljs-attr">methods</span>: {
    beforeHandleClose1 (hide) {
      alert(<span class="hljs-string">'before close'</span>)
      hide()
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>居中布局</h2>
<p>标题和底部可水平居中(内容是默认不居中的)</p>
<div class="demo-block">
<vi-button type="text" @click="dialogPage.dialogVisible2 = true">点击打开 dialog2</vi-button>
<vi-dialog title="提示" center="" :visible.sync="dialogPage.dialogVisible2" width="30%" :before-close="beforeHandleClose1">
  <span>dialog2 这是一段信息</span>
  <span slot="footer" class="dialog-footer">
    <vi-button @click="dialogPage.dialogVisible2 = false">取 消</vi-button>
    <vi-button type="primary" @click="dialogPage.dialogVisible2 = false">确 定</vi-button>
  </span>
</vi-dialog>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div class="demo-block">
<vi-button type="text" @click="dialogPage.dialogVisible2 = true">点击打开 dialog2</vi-button>
<vi-dialog title="提示" center="" :visible.sync="dialogPage.dialogVisible2" width="30%" :before-close="beforeHandleClose1">
  <span>dialog2 这是一段信息</span>
  <span slot="footer" class="dialog-footer">
    <vi-button @click="dialogPage.dialogVisible2 = false">取 消</vi-button>
    <vi-button type="primary" @click="dialogPage.dialogVisible2 = false">确 定</vi-button>
  </span>
</vi-dialog>
</div>

</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-block"</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">vi-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"dialogPage.dialogVisible2 = true"</span>&gt;</span>点击打开 dialog2<span class="hljs-tag">&lt;/<span class="hljs-name">vi-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">vi-dialog</span>
