  <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"footer"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"dialog-footer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vi-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"dialogPage.dialogVisible2 = false"</span>&gt;</span>取 消<span class="hljs-tag">&lt;/<span class="hljs-name">vi-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vi-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"dialogPage.dialogVisible2 = false"</span>&gt;</span>确 定<span class="hljs-tag">&lt;/<span class="hljs-name">vi-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">vi-dialog</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
