      <span class="hljs-attr">dialogPage</span>: {
        <span class="hljs-attr">dialogVisible2</span>: <span class="hljs-literal">false</span>
      }
    }
  },
  <span class="hljs-attr">methods</span>: {
    beforeHandleClose1 (hide) {
      alert(<span class="hljs-string">'before close'</span>)
